@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@500;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
  	--colorOne: #C6CAC9;
  	--colorTwo: #7E8486;
  	--colorThree: #45494E;
  	--colorFour: #D3A95E;
  	--colorFive: #FBE168;
  	--colorSix: #F5F5F5;
	--colorWhite: #FFFFFF;
	--colorBlack: #000000;
}

.body {
  	-ms-overflow-style: none;
  	scrollbar-width: none;
}

::-webkit-scrollbar {
  	display: none;
}

.App {
	text-align: center;
	font-family: 'Raleway', sans-serif;
	font-weight: 500;
	  
	display: flex;
	flex-direction: column;

	overflow: hidden;
	width: 100vw;
}

.App::-webkit-scrollbar {
  	display: none;
}

.container {
	max-width: 80%;
}

.hidden {
	display: none;
}

.app-top {
	display: hidden;
}
.home-parallax {
    height: 50vh;

    background-image: url(/static/media/hero-depot-color-1366x768.5d6be978.jpg);
    background-attachment: fixed;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}
#hero {
    height: 100vh;
    z-index: -1;
}

.jumbotron {
    position: relative;
    height: 100%;
    width: 100%;
  

    background-image: url(/static/media/hero-depot-bw-1366x768.502710b0.jpg);
    background-attachment: fixed;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}

.hero-message-container {

    position: absolute;
    top: 75%;
    width: 100%;

    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;

    background-color: rgba(0, 0, 0, 0.4);

    z-index: 1;
}

.hero-message {
    width: 70vw;
}

.hero-message img {
    width: 100%;
}

.hero-tracks-one, .hero-tracks-two {

    width: 4vw;
    height: 1.4vw;
}

.hero-tracks-one {
    margin-right: -20vw;
    margin-top: 4vw;

    -webkit-animation-name: hero-tracks-one-bounce;

            animation-name: hero-tracks-one-bounce;
    -webkit-animation-duration: 300ms;
            animation-duration: 300ms;
    -webkit-animation-delay: 1500ms;
            animation-delay: 1500ms;
    -webkit-animation-timing-function: linear;
            animation-timing-function: linear;
}

.hero-tracks-two {
    margin-left: -20vw;
    margin-top: 4vw;

    -webkit-animation-name: hero-tracks-two-bounce;

            animation-name: hero-tracks-two-bounce;
    -webkit-animation-duration: 300ms;
            animation-duration: 300ms;
    -webkit-animation-delay: 1500ms;
            animation-delay: 1500ms;
    -webkit-animation-timing-function: linear;
            animation-timing-function: linear;
}

@media screen and (max-width: 843px) {
    .hero-message-container {
        top: 70%;
    }
}

@-webkit-keyframes hero-tracks-one-bounce {
    0% {
        width: 4vw;
        height: 1.4vw;
        margin-right: -20vw;
    }

   32% {
        width: 4vw;
        height: 1.4vw;
        margin-right: -27.5vw;
    }

    50% {
        width: 3.5vw;
        height: 1.6vw;
        margin-right: -28vw;
    }

    48% {
        width: 4vw;
        height: 1.4vw;
        margin-right: -27.5vw;
    }

    80% {
        width: 4vw;
        height: 1.4vw;
        margin-right: -20vw;
    }

    90% {
        width: 4vw;
        height: 1.4vw;
        margin-right: -18vw;
    }

    100% {
        width: 4vw;
        height: 1.4vw;
        margin-right: -20vw;
    }
}

@keyframes hero-tracks-one-bounce {
    0% {
        width: 4vw;
        height: 1.4vw;
        margin-right: -20vw;
    }

   32% {
        width: 4vw;
        height: 1.4vw;
        margin-right: -27.5vw;
    }

    50% {
        width: 3.5vw;
        height: 1.6vw;
        margin-right: -28vw;
    }

    48% {
        width: 4vw;
        height: 1.4vw;
        margin-right: -27.5vw;
    }

    80% {
        width: 4vw;
        height: 1.4vw;
        margin-right: -20vw;
    }

    90% {
        width: 4vw;
        height: 1.4vw;
        margin-right: -18vw;
    }

    100% {
        width: 4vw;
        height: 1.4vw;
        margin-right: -20vw;
    }
}

@-webkit-keyframes hero-tracks-two-bounce {
    0% {
        width: 4vw;
        height: 1.4vw;
        margin-left: -20vw;
    }

   40% {
        width: 4vw;
        height: 1.4vw;
        margin-left: -27.5vw;
    }

    50% {
        width: 3.5vw;
        height: 1.6vw;
        margin-left: -28vw;
    }

    60% {
        width: 4vw;
        height: 1.4vw;
        margin-left: -27.5vw;
    }

    100% {
        width: 4vw;
        height: 1.4vw;
        margin-left: -20vw;
    }
}

@keyframes hero-tracks-two-bounce {
    0% {
        width: 4vw;
        height: 1.4vw;
        margin-left: -20vw;
    }

   40% {
        width: 4vw;
        height: 1.4vw;
        margin-left: -27.5vw;
    }

    50% {
        width: 3.5vw;
        height: 1.6vw;
        margin-left: -28vw;
    }

    60% {
        width: 4vw;
        height: 1.4vw;
        margin-left: -27.5vw;
    }

    100% {
        width: 4vw;
        height: 1.4vw;
        margin-left: -20vw;
    }
}
.up {
    position: relative;
    top: 50px;
    
    opacity: 0;

    margin: 0;
    padding: 0;
}

.down {
    position: relative;
    top: -50px;

    opacity: 0;

    margin: 0;
    padding: 0;
}

.after-fade {
    top: 0;

    opacity: 1;

    transition-property: top, opacity;
    transition-duration: 1200ms;
    transition-timing-function: ease-out;
}
.left {
    position: relative;
    left: -100vw;

    margin: 0;
    padding: 0;
}

.right {
    position: relative;
    left: 100vw;

    margin: 0;
    padding: 0;
}

.after-slide-linear {
    left: 0;

    transition-property: left;
    transition-duration: 1000ms;
    transition-timing-function: linear;


}

.after-slide-ease {
    left: 0;

    transition-property: left;
    transition-duration: 1000ms;
    transition-timing-function: ease-out;


}
#welcome {
    display: flex;
    flex-direction: row;
    justify-content: center;

    width: 90%;
    min-height: 55vh;
    margin-left: auto;
    margin-right: auto;
    margin-top: 100px;


}

.welcome-images, .welcome-text {
    max-width: 35%;
}

.welcome-images img {
    width: 300px;
}

.welcome-text {
    text-align: left;
    line-height: 1.5rem;
    letter-spacing: 1px;
}

.welcome-spacer {
    width: 20px;
}

.welcome-text h3 {
    margin: 0;
    padding: 0;
    margin-top: 10px;

    color: var(--colorFour);
    font-size: 1.8rem;
    font-weight: 500;

}

.welcome-text a {
    text-decoration: none;
    color: var(--colorFour);
    font-weight: 700;
}

.small-tracks {
    margin: 0;
    padding: 0;
    width: 150px;
    height: auto;
}

.welcome-full-tracks-container {
    position: absolute;
    left: 0;
    width: 100%;
}

.full-tracks {
    position: absolute;
    left: 0;
    top: 35vh;

    z-index: -1;

    height: 25px;
    width: 100%;
}

@media screen and (max-width: 1632px) {
    .welcome-images, .welcome-text {
        max-width: 45%;
    }
}

@media screen and (max-width: 1270px) {
    #welcome {
        flex-direction: column-reverse;
        align-items: center;
    }

    .welcome-images, .welcome-text {
        max-width: 60%;
    }

    .welcome-images img {
        max-width: 100%;
    }

    .full-tracks {
        display: none;
    }
}

